'use client'
import { useState, useEffect } from "react";
import { DashboardLayout } from "@/components/dashboard/dashboard-layout";
import { DashboardHeader } from "@/components/dashboard/dashboard-header";
import { Button } from "@/components/ui/button";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Card, CardContent, CardFooter } from "@/components/ui/card";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Textarea } from "@/components/ui/textarea";
import { Input } from "@/components/ui/input";
import { useSearchParams, useRouter } from "next/navigation"; 
import { 
  ThumbsUp, 
  MessageSquare, 
  Share2, 
  MapPin, 
  Plus, 
  X, 
  Loader2,
  AlertCircle,
  Tag
} from "lucide-react";
import { useToast } from "@/components/ui/use-toast";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { useMutation, useQuery } from '@apollo/client';
import { Badge } from "@/components/ui/badge";
import FileUploader from "@/components/uploads/fileuploader";
import { 
  GET_ALL_POSTS,
  GET_MY_POSTS,
  GET_SAVED_POSTS,
  CREATE_POST,
  LIKE_POST
} from '@/app/apollo/mutations/postmutations';
import ProtectedRoute from "@/components/ProtectedRoute";
import { IS_LOGGED_IN } from '@/app/apollo/operations/auth';

export default function PostsPage() {
  const searchParams = useSearchParams();
  const router = useRouter();
  const roleParam = searchParams.get('role');
  const [userRole, setUserRole] = useState('student');
  const [postContent, setPostContent] = useState("");
  const [location, setLocation] = useState("");
  const [showLocationInput, setShowLocationInput] = useState(false);
  const [activeTab, setActiveTab] = useState("all");
  const [uploadError, setUploadError] = useState(null);
  const [tags, setTags] = useState([]);
  const [tagInput, setTagInput] = useState("");
  const [showTagInput, setShowTagInput] = useState(false);
  const { toast } = useToast();
  const { data: authData, loading: authLoading } = useQuery(IS_LOGGED_IN);
  const currentUser = authData?.currentUser;
  const isLoggedIn = authData?.isLoggedIn;
  useEffect(() => {
    if (roleParam && ['student', 'university', 'company'].includes(roleParam)) {
      setUserRole(roleParam);
    } else {
      router.replace(`/posts?role=student`);
    }
  }, [roleParam, router]);

  const [createPost, { loading: isCreatingPost }] = useMutation(CREATE_POST, {
    refetchQueries: [{ query: GET_ALL_POSTS }, { query: GET_MY_POSTS, variables: { userId: currentUser?.id } }],
    onError: () => {
      setUploadError("Failed to create post. Please try again.");
      toast({ variant: "destructive", title: "Error", description: "Failed to create your post. Please try again." });
    }
  });

  const [likePost] = useMutation(LIKE_POST);
  const { data: allPostsData, loading: allPostsLoading } = useQuery(GET_ALL_POSTS);
  const { data: myPostsData, loading: myPostsLoading } = useQuery(GET_MY_POSTS, { variables: { userId: currentUser?.id }, skip: !currentUser?.id });
  const { data: savedPostsData, loading: savedPostsLoading } = useQuery(GET_SAVED_POSTS);
  const posts = allPostsData?.posts || [];
  const myPosts = myPostsData?.postsByUser || [];
  const savedPosts = savedPostsData?.posts || [];
  const [mediaUrls, setMediaUrls] = useState([]);

  const toggleLocationInput = () => {
    setShowLocationInput(!showLocationInput);
    if (showLocationInput) setLocation("");
  };

  const toggleTagInput = () => {
    setShowTagInput(!showTagInput);
    if (showTagInput) setTagInput("");
  };

  const handleAddTag = () => {
    if (tagInput.trim() !== "" && !tags.includes(tagInput.trim())) {
      setTags([...tags, tagInput.trim()]);
    }
    setTagInput("");
  };

  const removeTag = (tagToRemove) => {
    setTags(tags.filter(tag => tag !== tagToRemove));
  };

  const handleTagInputKeyPress = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      handleAddTag();
    }
  };

  const prepareLocationData = () => {
    return location ? { type: "Point", coordinates: [0, 0] } : null;
  };

  const handleCreatePost = async () => {
    if (!currentUser?.id || postContent.trim() === '') return;
    try {
      await createPost({
        variables: {
          userId: currentUser.id,
          content: postContent,
          tags: tags,
          location: prepareLocationData()
        }
      });
      toast({ title: "Post created successfully", description: "Your post has been published" });
      setPostContent("");
      setLocation("");
      setShowLocationInput(false);
      setTags([]);
      setShowTagInput(false);
    } catch (error) {
      console.error("Error creating post:", error);
    }
  };

  const handleLikePost = async (postId) => {
    if (!currentUser?.id) {
      toast({ variant: "destructive", title: "Authentication required", description: "You must be logged in to like a post" });
      return;
    }
    try {
      await likePost({ variables: { postId, userId: currentUser.id } });
    } catch (error) {
      toast({ variant: "destructive", title: "Error", description: "Failed to like the post. Please try again." });
    }
  };

  const getRoleSpecificContent = () => {
    const roleName = userRole.charAt(0).toUpperCase() + userRole.slice(1);
    let postType = "Posts";
    let postPlaceholder = "What's on your mind?";
    if (userRole === 'company') {
      postType = "Job Postings";
      postPlaceholder = "Share a job opportunity...";
    } else if (userRole === 'university') {
      postType = "Announcements";
      postPlaceholder = "Share an announcement with students...";
    }
    return { roleName, postType, postPlaceholder };
  };

  const { roleName, postType, postPlaceholder } = getRoleSpecificContent();

  if (authLoading) {
    return (
      <div className="flex justify-center items-center h-screen">
        <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
      </div>
    );
  }

  return (
    <ProtectedRoute allowedRoles={['student', 'university', 'company', 'admin']}>
      <DashboardLayout>
        <DashboardHeader heading={`${postType}`} text={`Share updates, questions, and connect with your community.`} />
        <Card>
          <CardContent className="p-6">
            <div className="flex gap-4">
              <Avatar className="h-10 w-10">
                <AvatarImage src={currentUser?.profilePicture || "/placeholder.png"} alt={currentUser?.firstName || "User"} />
                <AvatarFallback>{currentUser?.firstName?.[0] || "U"}</AvatarFallback>
              </Avatar>
              <div className="flex-1">
                <Textarea
                  placeholder={postPlaceholder}
                  className="min-h-[100px] resize-none mb-3"
                  value={postContent}
                  onChange={(e) => setPostContent(e.target.value)}
                />
              </div>
            </div>
            <div className="flex gap-2 mt-4">
              <Button size="sm" variant="ghost" onClick={toggleLocationInput}><MapPin className="mr-2 h-4 w-4" /> Location</Button>
              <Button size="sm" variant="ghost" onClick={toggleTagInput}><Tag className="mr-2 h-4 w-4" /> Tags</Button>
            </div>
            {showLocationInput && <Input className="mt-2" placeholder="Enter location" value={location} onChange={(e) => setLocation(e.target.value)} />}
            {showTagInput && (
              <div className="mt-2">
                <Input
                  placeholder="Add a tag and press Enter"
                  value={tagInput}
                  onChange={(e) => setTagInput(e.target.value)}
                  onKeyDown={handleTagInputKeyPress}
                />
                <div className="mt-2 flex gap-2 flex-wrap">
                  {tags.map(tag => (
                    <Badge key={tag} variant="secondary">{tag} <X className="ml-1 h-3 w-3 cursor-pointer" onClick={() => removeTag(tag)} /></Badge>
                  ))}
                </div>
              </div>
            )}
            <Button className="mt-4" disabled={isCreatingPost} onClick={handleCreatePost}>
              {isCreatingPost ? <Loader2 className="h-4 w-4 animate-spin mr-2" /> : "Post"}
            </Button>
          </CardContent>
        </Card>
      </DashboardLayout>
    </ProtectedRoute>
  );
}







